import trailerStars from "../../components/trailerStars.vue"
export default {
	data() {
		return {
			carouselList: [],
			hotSuperheroList: [],
			hotTrailerList: [],
			guessULikeList: [],
			animationData: {},
			animationDataArr: [{}, {}, {}, {}, {}]

		}
	},
	onUnload() {
		//页面卸载的时候，清除动画数据
		this.animationData = {};
		this.animationDataArr = [{}, {}, {}, {}, {}]
	},
	onPullDownRefresh() {
		this.refresh()
	},
	onHide() {
		if (this.videoContext) {
			this.videoContext.pause()
		}
	},
	onLoad() {
		var that = this;
		//在页面创建的时候，创建一个临时动画对象

		this.animation = uni.createAnimation()

		//通过挂载到main.js中获取服务器的地址，作为全局变量
		var serverURL = that.serverURL;
		//请求轮播图数据
		uni.request({
			url: serverURL + '/index/carousel/list?qq=lee82223437',
			method: "POST",
			success: (res) => {
				if (res.data.status == 200) {
					var carouselList = res.data.data
					that.carouselList = carouselList
				}

			}
		});
		//请求热门超英
		uni.request({
			url: serverURL + '/index/movie/hot?type=superhero&qq=lee82223437',
			method: "POST",
			success: (res) => {
				if (res.data.status == 200) {
					var hotSuperheroList = res.data.data
					this.hotSuperheroList = hotSuperheroList
				}
			},

		})
		//请求超英预告
		uni.request({
			url: serverURL + '/index/movie/hot?type=trailer&qq=lee82223437',
			method: "POST",
			success: (res) => {
				if (res.data.status == 200) {
					var hotTrailerList = res.data.data
					this.hotTrailerList = hotTrailerList
				}

			}
		})
		//查询猜你喜欢
		that.refresh()
	},
	methods: {
		//播放一个视频的时候，需要暂停其他正在播放的视频(通过判断是否是当前id)
		meIsPlaying(e) {
			var that = this;
			var trailerId = "";
			if (e) {
				trailerId = e.currentTarget.dataset.playingindex
			}
			console.log(trailerId)
			var hotTrailerList = that.hotTrailerList;
			for (var i = 0; i < hotTrailerList.length; i++) {
				var tempId = hotTrailerList[i].id;
				if (tempId != trailerId) {
					uni.createVideoContext(tempId).pause()
				}
			}
		},
		refresh() {
			var that = this;
			uni.showLoading({
				mask: true
			})
			uni.showNavigationBarLoading()
			var serverURL = that.serverURL;
			uni.request({
				url: serverURL + '/index/guessULike?qq=lee82223437',
				method: "POST",
				success: (res) => {
					if (res.data.status == 200) {
						var guessULikeList = res.data.data
						this.guessULikeList = guessULikeList
					}
				},
				complete: () => {
					uni.hideNavigationBarLoading();
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			})
		},
		//实现点赞动画效果
		praiseMe(e) {

			var gIndex = e.currentTarget.dataset.gindex;
			//构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-60).opacity(1).step({
				duration: 400
			});
			//导出动画数据到view组件，实现组建的动画效果
			this.animationData = this.animation
			this.animationDataArr[gIndex] = this.animationData.export()
			//还原动画
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				});
				this.animationData = this.animation
				this.animationDataArr[gIndex] = this.animationData.export()
			}.bind(this), 500);

		}

	},
	components: {
		trailerStars
	}
}
